@import url(https://cdnjs.cloudflare.com/ajax/libs/normalize/8.0.1/normalize.min.css);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@500;700&display=swap);
.loader__layout{height:56px;text-align:center}.loader__body{width:56px;height:56px;margin:0 auto;border:5px solid;border-radius:50%;border-color:#fff8dc;-webkit-animation:cssload-scale 1035ms infinite linear;animation:cssload-scale 1035ms infinite linear}@-webkit-keyframes cssload-scale{0%{transform:scale(0);transform:scale(0)}90%{transform:scale(0.7);transform:scale(0.7)}100%{transform:scale(1);transform:scale(1)}}@keyframes cssload-scale{0%{transform:scale(0);transform:scale(0)}90%{transform:scale(0.7);transform:scale(0.7)}100%{transform:scale(1);transform:scale(1)}}

.quote__layout{display:grid;grid-template-rows:130px 180px;grid-template-columns:1fr;place-items:center}@media screen and (min-width: 600px){.quote__layout{grid-template-columns:1fr 180px;grid-template-rows:180px;width:600px;padding:30px;box-shadow:0 0 10px rgba(102,101,101,0.5);background:#fff8dc}}@media screen and (min-width: 960px){.quote__layout{grid-template-columns:1fr 220px;grid-template-rows:220px;padding:50px;width:940px}}.quote__text{align-self:center;padding:0 18px;text-align:center;font-size:21px;font-weight:700;color:#854e2a}@media screen and (min-width: 600px){.quote__text{font-size:22px;font-style:italic;color:#546459}}@media screen and (min-width: 960px){.quote__text{font-size:28px}}.quote__character{margin-top:8px;font-size:20px;font-style:italic;color:#c26324}@media screen and (min-width: 600px){.quote__character{margin-top:18px;color:#90b49d}}@media screen and (min-width: 960px){.quote__character{font-size:24px}}.quote__character-image{width:180px}@media screen and (min-width: 960px){.quote__character-image{width:220px}}.next__button-layout{margin:26px 0 0;text-align:center}@media screen and (min-width: 600px){.next__button-layout{margin:36px 0 0}}.next__button{width:110px;height:50px;border:3px solid;border-radius:28px;background:#b87446;font-size:18px;font-weight:500;color:#f5deb3;transition:0.1s linear;cursor:pointer}.next__button:hover,.next__button:focus,.next__button:active{background:#a06137}@media screen and (min-width: 600px){.next__button{width:160px;height:50px;background:#e90856;font-size:20px;color:#fff8dc}.next__button:hover,.next__button:focus,.next__button:active{background:#ce074d}}@media screen and (min-width: 960px){.next__button{width:180px;height:60px;font-size:22px}}

html{box-sizing:border-box}*,*::after,*::before{box-sizing:inherit}body{font-family:"Rubik", sans-serif}.app__layout{display:grid;place-content:center;min-height:100vh;background-image:url(/friends-quotes/static/media/background-mobile.d86fec6b.jpg);background-position:center;background-size:cover}@media screen and (min-width: 600px){.app__layout{background-image:url(/friends-quotes/static/media/background-desktop.845b86a6.jpg)}}

